        "about_me": "<p><a href=\"http://blog.stackoverflow.com/2010/06/welcome-stack-overflow-valued-associates-00006-and-00007/\">Stack Overflow Valued Associate #00006</a></p>\n\n<p>e-mail: marc.gravell@gmail.com or marc@stackoverflow.com<br/>\nblog: <a href=\"http://blog.marcgravell.com/\" rel=\"nofollow noreferrer\">http://blog.marcgravell.com/</a><br/>\ntalks: <a href=\"http://marcgravell.com/\" rel=\"nofollow noreferrer\">http://marcgravell.com/</a><br/>\ntwitter: <a href=\"http://twitter.com/marcgravell\" rel=\"nofollow noreferrer\">@marcgravell</a><br/>\ncareers: <a href=\"http://careers.stackoverflow.com/marcgravell\">marcgravell</a></p>\n\n<p>C# programmer and MVP, with a keen interest in all things code.\nParticular areas:<ul>\n<li><a href=\"https://github.com/StackExchange/dapper-dot-net\" rel=\"nofollow noreferrer\">Dapper</a> (<a href=\"/questions/tagged/dapper\" class=\"post-tag\" title=\"show questions tagged &#39;dapper&#39;\" rel=\"tag\">dapper</a>)\n<li><a href=\"https://github.com/StackExchange/StackExchange.Redis\" rel=\"nofollow noreferrer\">StackExchange.Redis</a> (<a href=\"/questions/tagged/stackexchange.redis\" class=\"post-tag\" title=\"show questions tagged &#39;stackexchange.redis&#39;\" rel=\"tag\">stackexchange.redis</a>)\n<li><a href=\"http://code.google.com/p/protobuf-net/\" rel=\"nofollow noreferrer\">Protocol Buffers</a> (<a href=\"/questions/tagged/protobuf-net\" class=\"post-tag\" title=\"show questions tagged &#39;protobuf-net&#39;\" rel=\"tag\">protobuf-net</a>)\n</ul></p>\n",
