        "about_me": "<p>Mark Murphy, founder of CommonsWare, writes a lot.</p>\n<h2>Second-Generation Books</h2>\n<p>His most recent books have been focused on second-generation Android app development (Kotlin and the Android Jetpack):</p>\n<ul>\n<li><p><a href=\"https://commonsware.com/Jetpack\" rel=\"nofollow noreferrer\"><em>Elements of Android Jetpack</em></a>, a new introductory book on Android app development, focused on second-generation techniques. Version 1.9 is available, with 898 pages, covering Android Studio 4.1.1 and up through Android 11.</p>\n</li>\n<li><p><a href=\"https://commonsware.com/AndExplore\" rel=\"nofollow noreferrer\"><em>Exploring Android</em></a>, a series of step-by-step tutorials for building an Android app from scratch. This book is being rewritten to demonstrate using Kotlin and the Jetpack libraries. Version 2.0 is available, with 34 tutorials spanning 610 pages.</p>\n</li>\n<li><p><a href=\"https://commonsware.com/R\" rel=\"nofollow noreferrer\"><em>Elements of Android R</em></a>, to help you deal with the changes introduced by Android 11 (code-named &quot;R&quot;). The final version is available, with 140 pages of material on everything from scoped storage changes to data access auditing to sharing UIs between apps to bubbles.</p>\n</li>\n<li><p><a href=\"https://commonsware.com/Room\" rel=\"nofollow noreferrer\"><em>Elements of Android Room</em></a>, to help you incorporate Google's reactive object wrapper around SQLite. Version 0.3 is available, comprising 188 pages.</p>\n</li>\n<li><p><a href=\"https://commonsware.com/Kotlin\" rel=\"nofollow noreferrer\"><em>Elements of Kotlin</em></a>, a guide to the Kotlin programming language, which is rapidly gaining in popularity for Android app development. Version 0.3 is available, comprising 372 pages.</p>\n</li>\n<li><p><a href=\"https://commonsware.com/Coroutines\" rel=\"nofollow noreferrer\"><em>Elements of Kotlin Coroutines</em></a>, exploring Kotlin's new\nfirst-class reactive programming system. Version 0.2 is available, comprising 236 pages.</p>\n</li>\n</ul>\n<h2>First-Generation Books</h2>\n<p>In the beginning, Android development was focused on Java and, partially, on the Android Support Library. Mark's earliest books share that focus:</p>\n<ul>\n<li><p><a href=\"https://commonsware.com/Android\" rel=\"nofollow noreferrer\"><em>The Busy Coder's Guide to Android Development</em></a>, the first and largest book on Android app programming</p>\n</li>\n<li><p><a href=\"https://commonsware.com/AndroidArch\" rel=\"nofollow noreferrer\"><em>Android's Architecture Components</em></a></p>\n</li>\n<li><p><a href=\"https://commonsware.com/GraphQL\" rel=\"nofollow noreferrer\"><em>GraphQL and Android</em></a></p>\n</li>\n</ul>\n<h2>Things Other Than Books</h2>\n<p>He runs the <a href=\"https://jetc.dev\" rel=\"nofollow noreferrer\"><code>jetc.dev</code></a> weekly newsletter for Jetpack Compose.</p>\n<p>He also offers <a href=\"https://commonsware.com/office-hours/\" rel=\"nofollow noreferrer\">office hours chats</a> and <a href=\"https://commonsware.com/consulting\" rel=\"nofollow noreferrer\">consulting</a> on Android app development, in case you are interested in help beyond what fits the Stack Overflow model, the books, and the training.</p>\n<p>Heck, he even has <a href=\"http://commonsware.com/blog\" rel=\"nofollow noreferrer\">a blog</a>.</p>\n<p>All code in Stack Overflow questions, answers, or comments written by Mark Murphy are hereby licensed under <a href=\"http://www.apache.org/licenses/LICENSE-2.0\" rel=\"nofollow noreferrer\">the Apache Software License 2.0</a>, unless otherwise noted where that code appears on Stack Overflow.</p>\n",
