        "about_me": "<p><a href=\"http://Careers.StackOverflow.com/SLaks\"> <h1> Schabse Laks</h1> <i>Developer, C# MVP (2010 - 2018)</i> </a> <br /> <img src=\"https://slaks.net/Blog-Content/Images/MVPLogo.png\" alt=\"MVP\" /><br /> Contact: SLaks@LaksFamily.com</p>\n\n<p>Twitter: <a href=\"https://twitter.com/Schabse\" rel=\"nofollow noreferrer\">@Schabse</a></p>\n\n<p><p> I've been programming since I was 12.<br /> I started with VB6, then moved to C# 2 Beta 2 when I was 14. I never liked VB6, but I found my calling in C#, and I've stuck with it ever since.<br /> Since then, I've also mastered web development with jQuery.<br>\nI also wrote a <a href=\"http://jqpresentation.slaks.net/Demos/Introducing-jQuery/\" rel=\"nofollow noreferrer\">jQuery tutorial</a>.</p></p>\n\n<p>I'm now working at Google within the Google Docs team.</p>\n\n<p><p> My best of SO: <ul> <li><a href=\"http://stackoverflow.com/questions/2508945/can-anyone-explain-this-strange-behaviour/2509174#2509174\"> Tracking down a bug in the .Net runtime</a></li> <li><a href=\"http://stackoverflow.com/questions/1563516/um-proggy-not-worky/1563668#1563668\"> An exercise in psychic debugging (see the question history)</a></li> <li><a href=\"http://stackoverflow.com/questions/1331739/enum-type-constraints-in-c/1416660#1416660\" title=\"Enum type constraints in C#\">Working around a C# compiler limitation</a></li> <li><a href=\"http://stackoverflow.com/questions/2117747/how-does-this-mess-work/2117775#2117775\"> Decoding a Javascript virus</a></li> <li><a href=\"http://stackoverflow.com/questions/2492271/can-this-jquery-code-snippet-be-shortened/2492284#2492284\"> jQuery selector magic</a></li> <li><a href=\"http://stackoverflow.com/questions/2113501/silverlight-confirm-dialog-to-pause-thread/2113555#2113555\"> Simple coroutines using iterators</a></li> <li><a href=\"http://stackoverflow.com/questions/2204538/what-is-click-in-javascript/2204568#2204568\"> Understanding Javascript scoping in inline event handlers</a></li> <li><a href=\"http://stackoverflow.com/questions/2140882/vertically-only-resizable-windows-form-in-c/2140908#2140908\" title=\"Vertically (only) resizable windows form in C#\"><code>WM_NCHITTEST</code> magic</a></li> <li><a href=\"http://stackoverflow.com/questions/1600662/this-null-in-c\" title=\"(this == null) in C#!\"> A bug in the C# compiler</a></li> </ul> </p></p>\n",
