          "body": "<p>I am trying to implement feedback for users of my app using <code>shinyFeedback</code>. I use custom currency formatting of numeric inputs from <code>shinyWidgets</code>. These two do not seem to work together. Here is a reprex showing <code>shinyFeedback</code> working correctly with <code>numericInput</code>, but not working with <code>autonumericInput</code>.</p>\n<p><a href=\"https://i.stack.imgur.com/mvMJe.png\" rel=\"nofollow noreferrer\"><img src=\"https://i.stack.imgur.com/mvMJe.png\" alt=\"undesired behaviour\" /></a></p>\n<pre><code>library(shiny)\nlibrary(shinyWidgets)\nlibrary(shinyFeedback)\n\nui &lt;- fluidPage(\n  useShinyFeedback(),\n  numericInput(\n    &quot;numeric_input&quot;, &quot;numeric_input&quot;,\n    value = 1e6, min = -Inf, max = Inf\n  ),\n  autonumericInput(\n    &quot;autonumeric_input&quot;, &quot;autonumeric_input&quot;,\n    value = 1e6, currencySymbol = &quot;EUR &quot;, currencySymbolPlacement = &quot;s&quot;, decimalPlaces = 0, digitGroupSeparator = &quot; &quot;\n  ),\n)\n\nserver &lt;- function(input, output, session) {\n\n  observeEvent(input$numeric_input, {\n    feedbackWarning(&quot;numeric_input&quot;, show = input$numeric_input &gt;= 0, text = &quot;Hello numeric_input&quot;)\n  })\n  \n  observeEvent(input$autonumeric_input, {\n    feedbackWarning(&quot;autonumeric_input&quot;, show = input$autonumeric_input &gt;= 0, text = &quot;Hello autonumeric_input&quot;)\n  })\n\n}\n\nshinyApp(ui, server)\n</code></pre>\n"
