          "title": "Avoid recursion error with cv2 and mouse callback",
          "body": "<p>I have a class for opening up an image and drawing circles. The entire code can be found here:</p>\n<p><code>p1</code> and <code>p2</code> store diametrically opposing points on a circle. These are capture with click and drag actions in <code>_on_mouse_interact</code>.</p>\n<pre><code>import sys\nimport math\n\nimport cv2\n\n\nclass DrawCircle:\n    def __init__(self):\n        self.p1 = None\n        self.p2 = None\n        self.clicked = False\n\n    def __call__(self, img):\n        self.img = img\n        self.redraw()\n        return self.p1, self.p2\n\n    def redraw(self):\n        drawn = self.img.copy()\n        if self.p1 is not None and self.p2 is not None:\n            center = (int(round((self.p1[0] + self.p2[0]) / 2)),\n                        int(round((self.p1[1] + self.p2[1]) / 2)))\n            radius = int(math.sqrt(\n                (self.p1[0] - self.p2[0])**2 + (self.p1[1] - self.p2[1])**2) / 2)\n            cv2.circle(drawn, center, radius, (0, 0, 255), thickness=1)\n        window_name = 'draw circle'\n        cv2.imshow(window_name, drawn)\n        cv2.setMouseCallback(window_name, self._on_mouse_interact)\n        k = cv2.waitKey(0)\n        if k == ord('q'):\n            cv2.destroyAllWindows()\n            return\n\n    def _on_mouse_interact(self, event, x, y, flags, param):\n        if event == cv2.EVENT_LBUTTONDOWN:\n            self.clicked = True\n            self.p1 = [x, y]\n            self.p2 = None\n        if event == cv2.EVENT_MOUSEMOVE:\n            if self.clicked:\n                self.p2 = [x, y]\n                self.redraw()\n        if event == cv2.EVENT_LBUTTONUP:\n            self.clicked = False\n            self.p2 = [x, y]\n            self.redraw()\n\n\nif __name__ == '__main__':\n    draw_tool = DrawCircle()\n    img = cv2.imread(sys.argv[1])\n    draw_tool(img)\n</code></pre>\n<p>It can be run with</p>\n<pre><code>python script.py path/to/image/file\n</code></pre>\n<p>My issue is that I get</p>\n<pre><code>Traceback (most recent call last):\n  File &quot;utils/realtime_draw.py&quot;, line 47, in _on_mouse_interact\n    self.redraw()\n  File &quot;utils/realtime_draw.py&quot;, line 24, in redraw\n    drawn = self.img.copy()\nRecursionError: maximum recursion depth exceeded while calling a Python object\n</code></pre>\n<p>Where is the recursion here?</p>\n"
