          "title": "Why does mock patch only work when running specific test and not whole test suite?",
          "body": "<p>I'm using Django and Pytest specifically to run the test suite and am trying to test that a specific form shows up with expected data when a user hits the site (integration test).</p>\n<p>This particular view uses a stored procedure, which I am mocking since the test would never have access to that.</p>\n<p>My test code looks like this:</p>\n<pre><code>#test_integrations.py\n\nfrom my_app.tests.data_setup import setup_data, setup_sb7_data\nfrom unittest.mock import patch\n\n...\n\n# Setup to use a non-headless browser so we can see whats happening for debugging\n@pytest.mark.usefixtures(&quot;standard_browser&quot;)\nclass SeniorPageTestCase(StaticLiveServerTestCase):\n    &quot;&quot;&quot;\n    These tests surround the senior form\n    &quot;&quot;&quot;\n\n    @classmethod\n    def setUpClass(cls):\n        cls.host = socket.gethostbyname(socket.gethostname())\n        super(SeniorPageTestCase, cls).setUpClass()\n\n    def setUp(self):\n        # setup the dummy data - this works fine\n        basic_setup(self)\n        # setup the 'results'\n        self.sb7_mock_data = setup_sb7_data(self)\n\n    @patch(&quot;my_app.utils.get_employee_sb7_data&quot;)\n    def test_senior_form_displays(self, mock_sb7_get):\n        # login the dummy user we created\n        login_user(self, &quot;futureuser&quot;)\n        # setup the results\n        mock_sb7_get.return_value = self.sb7_mock_data\n        # hit the page for the form\n        self.browser.get(self.live_server_url + &quot;/my_app/senior&quot;)\n        form_id = &quot;SeniorForm&quot;\n        # assert that the form displays on the page\n        self.assertTrue(self.browser.find_element_by_id(form_id))\n</code></pre>\n<pre><code># utils.py\n\nfrom django.conf import settings\nfrom django.db import connections\n\n\ndef get_employee_sb7_data(db_name, user_number, window):\n    &quot;&quot;&quot;\n    Executes the stored procedure for getting employee data\n\n    Args:\n        user_number: Takes the user_number\n        db (db connection): Takes a string of the DB to connect to\n\n    Returns:\n\n    &quot;&quot;&quot;\n    cursor = connections[db_name].cursor()\n    cursor.execute(\n        'exec sp_sb7 %s, &quot;%s&quot;' % (user_number, window.senior_close)\n    )\n    columns = [col[0] for col in cursor.description]\n    results = [dict(zip(columns, row)) for row in cursor.fetchall()]\n    return results\n</code></pre>\n<pre><code># views.py\n\nfrom myapp.utils import (\n    get_employee_sb7_data,\n)\n\n...\n\n###### Senior ######\n@login_required\n@group_required(&quot;user_senior&quot;)\ndef senior(request):\n\n    # Additional Logic / Getting Other Models here\n\n    # Execute stored procedure to get data for user\n    user_number = request.user.user_no\n    results = get_employee_sb7_data(&quot;production_db&quot;, user_number, window)\n    if not results:\n        return render(request, &quot;users/senior_not_required.html&quot;)\n\n    # Additional view stuff\n\n    return render(\n        request,\n        &quot;users/senior.html&quot;,\n        {\n            &quot;data&quot;: data,\n            &quot;form&quot;: form,\n            &quot;results&quot;: results,\n        },\n    )\n</code></pre>\n<p>If I run this test itself with:</p>\n<p><code>pytest my_app/tests/test_integrations.py::SeniorPageTestCase</code></p>\n<p>The tests pass without issue. The browser shows up - the form shows up with the dummy data as we would expect and it all works.</p>\n<p>However, if I run:</p>\n<p><code>pytest my_app</code></p>\n<p>All other tests run and pass - but all the tests in this class fail because it's not patching the function.</p>\n<p>It tries to call the actual stored procedure (which fails because it's not on the production server yet) and it fails.</p>\n<p>Why would it patch correctly when I call that TestCase specifically - but not patch correctly when I just run <code>pytest</code> on the app or project level?</p>\n<p>I'm at a loss and not sure how to debug this very well. Any help is appreciated</p>\n"
