          "title": "Unmarshall an XML file and save to database in Java",
          "body": "<p>I have the following XML file being uploaded in Java to unmarshall:</p>\n<pre><code>&lt;?xml version=&quot;1.0&quot; encoding=&quot;UTF-8&quot; standalone=&quot;no&quot;?&gt;\n&lt;MyValues&gt;\n    &lt;Type Value=&quot;ABC&quot;&gt;\n        &lt;CustomerName&gt;Sample&lt;/CustomerName&gt;\n        &lt;EmailAddress&gt;sample@abcd.com&lt;/EmailAddress&gt;\n        &lt;PhoneNumber&gt;1234567890&lt;/PhoneNumber&gt;\n        &lt;HardwareKey&gt;abc-def-123-456&lt;/HardwareKey&gt;\n    &lt;/Type&gt;\n&lt;/MyValues&gt;\n</code></pre>\n<p>I want to upload this file's values to my database in my Spring Application. The Java code to unmarshall is:</p>\n<pre><code>String transformedFileName = TRANSFORMED_FILE_DIRECTORY+&quot;/&quot;+fileName;\n\nJAXBContext context = JAXBContext.newInstance(CustomerDetails.class);\n        Unmarshaller um = context.createUnmarshaller();\n\n        customerDetails = (CustomerDetails) um.unmarshal(new InputStreamReader(\n                new FileInputStream(transformedFileName), StandardCharsets.UTF_8));\n        \n        System.out.println(customerDetails);\n</code></pre>\n<p>and the CustomerDetails.java is:</p>\n<pre><code>\n@Entity\n@Table(name=&quot;customerDetails&quot;)\n@EntityListeners(AuditingEntityListener.class)\npublic class CustomerDetails {\n\n    @Id\n    @GeneratedValue(strategy=GenerationType.AUTO)\n    private Long id;\n    \n    private String type;\n    \n    private String hardwareKey;\n    \n    private String emailAddress;\n    \n    private Long phoneNumber;\n    \n    private String customerName;\n\n    public Long getId() {\n        return id;\n    }\n\n    public void setId(Long id) {\n        this.id = id;\n    }\n\n    public String getType() {\n        return type;\n    }\n\n    public void setType(String type) {\n        this.type = type;\n    }\n\n    public String getHardwareKey() {\n        return hardwareKey;\n    }\n\n    public void setHardwareKey(String hardwareKey) {\n        this.hardwareKey = hardwareKey;\n    }\n\n    public String getEmailAddress() {\n        return emailAddress;\n    }\n\n    public void setEmailAddress(String emailAddress) {\n        this.emailAddress = emailAddress;\n    }\n\n    public Long getPhoneNumber() {\n        return phoneNumber;\n    }\n\n    public void setPhoneNumber(Long phoneNumber) {\n        this.phoneNumber = phoneNumber;\n    }\n\n    public String getCustomerName() {\n        return customerName;\n    }\n\n    public void setCustomerName(String customerName) {\n        this.customerName = customerName;\n    }\n}\n\n</code></pre>\n<p>This is the error I am getting while running this:</p>\n<pre><code>javax.xml.bind.UnmarshalException: unexpected element (uri:&quot;&quot;, local:&quot;MyValues&quot;). Expected elements are (none)\n</code></pre>\n<p>I cannot change the file values and structure, and have to save the file values in database. How to overcome this error and how to save the values? What am I doing wrong?</p>\n"
