          "title": "Node.js async try/catch error when getting data from mongoose",
          "body": "<p>I'm trying to make a very basic login page that has three <strong>routes</strong> <strong>LOGIN</strong>, <strong>REGISTER</strong>, and <strong>WELCOME</strong>. The idea being that you can register a user with a <strong>name</strong>, <strong>email</strong>, and <strong>password</strong> of your choice at which point you will be redirected to login where you can use your information created before entering into the welcome page.</p>\n<p>Up until now, I've been saving the user information to an empty array as an object and then searching through it with <code>passport-config</code> to test the credentials. Obviously, this is not secure and also has no way of saving information after a restart of the server. To solve this, I started integrated <code>mongoose</code> into my project. It was working ok, but now, when trying to retrieve my user list in order to check it against the information passed by the user, I am getting the following error:</p>\n<pre><code>ReferenceError: Cannot access 'user' before initialization\n</code></pre>\n<p>this is really confusing for me because my code:</p>\n<pre><code>try {\n  const user = await User.find(new RegExp(user.userEmail, 'i'))\n  console.log(user)\n  if (user == null) {\n    return done(null, false, { message: &quot;No User With That Email&quot; })\n  }\n  try {\n    console.log(password, user.userPassword)\n    if (await bcrypt.compare(password, user.userPassword, function (e) { })) {\n      return done(null, user)\n    } else {\n      return done(null, false, { message: &quot;Password Incorrect&quot; })\n    }\n  } catch (e) {\n    return done(e)\n  }\n} catch (e) {\n  return done(e)\n}\n</code></pre>\n<p>is wrapped in a try/catch statement. There is also the bcrypt part which you can ignore, it's just for reference.</p>\n<p>Firstly, why is my try/catch still throwing an error, and secondly, why am I getting the <code>initialization</code> error?</p>\n<p>Thank you, I appreciate your replies.</p>\n"
