          "link": "https://stackoverflow.com/questions/61695769/why-using-view-binding-is-changing-the-layout",
          "title": "Why using View Binding is changing the layout?",
          "body": "<p>Actual Code:</p>\n\n<p>My Main Activity:</p>\n\n<pre class=\"lang-kotlin prettyprint-override\"><code>class MainActivity : AppCompatActivity() {\n    private lateinit var binding: ActivityMainBinding\n\n    override fun onCreate(savedInstanceState: Bundle?) {\n        super.onCreate(savedInstanceState)\n        binding = ActivityMainBinding.inflate(layoutInflater)\n\n        val rollButton = binding.rollButton\n        rollButton.setOnClickListener { rollDice() }\n\n        setContentView(binding.root)\n    }\n\n    private fun rollDice() {\n        val randomDiceRoll = Random.nextInt(6) + 1\n        Toast.makeText(this, randomDiceRoll.toString(), Toast.LENGTH_SHORT).show()\n        binding.resultText.text = randomDiceRoll.toString()\n    }\n</code></pre>\n\n<p>My XML file:</p>\n\n<pre class=\"lang-xml prettyprint-override\"><code>&lt;?xml version=\"1.0\" encoding=\"utf-8\"?&gt;\n&lt;LinearLayout xmlns:android=\"http://schemas.android.com/apk/res/android\"\n    xmlns:tools=\"http://schemas.android.com/tools\"\n    android:layout_width=\"match_parent\"\n    android:layout_height=\"wrap_content\"\n    android:layout_gravity=\"center_vertical\"\n    android:orientation=\"vertical\"\n    tools:context=\".MainActivity\"&gt;\n\n    &lt;TextView\n        android:id=\"@+id/result_text\"\n        android:layout_width=\"wrap_content\"\n        android:layout_height=\"wrap_content\"\n        android:layout_gravity=\"center_horizontal\"\n        android:text=\"@string/count\"\n        android:textSize=\"30sp\" /&gt;\n\n    &lt;Button\n        android:id=\"@+id/roll_button\"\n        android:layout_width=\"wrap_content\"\n        android:layout_height=\"wrap_content\"\n        android:layout_gravity=\"center_horizontal\"\n        android:text=\"@string/roll\" /&gt;\n\n&lt;/LinearLayout&gt;\n</code></pre>\n\n<p>Preview in android studio:</p>\n\n<p><a href=\"https://i.stack.imgur.com/uhQPI.png\" rel=\"nofollow noreferrer\"><img src=\"https://i.stack.imgur.com/uhQPI.png\" alt=\"Preview\"></a></p>\n\n<p>Actual layout in device:</p>\n\n<p><a href=\"https://i.stack.imgur.com/8eTOEm.jpg\" rel=\"nofollow noreferrer\"><img src=\"https://i.stack.imgur.com/8eTOEm.jpg\" alt=\"actual layout\"></a></p>\n\n<p>If I change <code>setContentView(binding.root)</code> to <code>setContentView(R.layout.activity_main)</code>, the layout of Android Studio and the device become the same, but of course, the button doesn't work anymore...</p>\n\n<p>Why does this happen? Why is <code>ActivityMainBinding.inflate(layoutInflater)</code> changing the layout? How to fix this? </p>\n\n<p>Thanks</p>\n\n<p>GitHub Repo:\n<a href=\"https://github.com/Wizard28082006/Dice_Roller\" rel=\"nofollow noreferrer\">https://github.com/Wizard28082006/Dice_Roller</a></p>\n"
