          "link": "https://stackoverflow.com/questions/65306008/create-unique-integers",
          "title": "Create unique integers",
          "body": "<p>Please help, I am trying to create unique numbers in python. I want to use this method which to me at least makes good sense. Thanks in advance and I have tried my best for 4 hours of painful screen staring before posting this :-)</p>\n<pre><code>def createOrderNum():\n    orderNum = random.randint(48,56)\n    filesize = os.path.getsize(&quot;customers.txt&quot;)\n    if filesize == 0:\n        print(&quot;The file is empty: &quot; + str(filesize))\n        return(orderNum)\n    else:\n        print(&quot;The file is not empty: &quot; + str(filesize))\n    exists = False\n    while exists == False:\n        with open('customers.txt', 'r') as f:\n            for line in f:\n                \n                if str(orderNum) == line.split(',')[1]:\n                    print('number exists')\n                    createOrderNum()\n                else:\n                    print('not exists')\n        if exists == False:\n            return orderNum\n</code></pre>\n"
