          "body": "<p>When a crash occurs in .net it seems that EventViewer doesn't contain the quite important Message part. Why is this? Am I missing something on how to get this information?</p>\n<p>For example I am running a simple WPF application with a button that just throws an exception like this:</p>\n<pre><code>private void Button_Click(object sender, RoutedEventArgs e)\n{\n     throw new NotImplementedException(&quot;Test message for event viewer.&quot;);\n}\n</code></pre>\n<p>But in EventViewer all I get is this and no sign of the message:</p>\n<pre><code>Application: WpfApp4.exe\nFramework Version: v4.0.30319\nDescription: The process was terminated due to an unhandled exception.\nException Info: System.NotImplementedException\n   at WpfApp4.MainWindow.Button_Click(System.Object, System.Windows.RoutedEventArgs)\n   at System.Windows.RoutedEventHandlerInfo.InvokeHandler(System.Object, System.Windows.RoutedEventArgs)\n   at System.Windows.EventRoute.InvokeHandlersImpl(System.Object, System.Windows.RoutedEventArgs, Boolean)\n   at System.Windows.UIElement.RaiseEventImpl(System.Windows.DependencyObject, System.Windows.RoutedEventArgs)\n   at System.Windows.UIElement.RaiseEvent(System.Windows.RoutedEventArgs)\n   at System.Windows.Controls.Primitives.ButtonBase.OnClick()\n   at System.Windows.Controls.Button.OnClick()\n   at System.Windows.Controls.Primitives.ButtonBase.OnMouseLeftButtonUp(System.Windows.Input.MouseButtonEventArgs)\n   at System.Windows.UIElement.OnMouseLeftButtonUpThunk(System.Object, System.Windows.Input.MouseButtonEventArgs)\n   at System.Windows.Input.MouseButtonEventArgs.InvokeEventHandler(System.Delegate, System.Object)\n   at System.Windows.RoutedEventArgs.InvokeHandler(System.Delegate, System.Object)\n   at System.Windows.RoutedEventHandlerInfo.InvokeHandler(System.Object, System.Windows.RoutedEventArgs)\n   at System.Windows.EventRoute.InvokeHandlersImpl(System.Object, System.Windows.RoutedEventArgs, Boolean)\n   at System.Windows.UIElement.ReRaiseEventAs(System.Windows.DependencyObject, System.Windows.RoutedEventArgs, System.Windows.RoutedEvent)\n   at System.Windows.UIElement.OnMouseUpThunk(System.Object, System.Windows.Input.MouseButtonEventArgs)\n   at System.Windows.Input.MouseButtonEventArgs.InvokeEventHandler(System.Delegate, System.Object)\n   at System.Windows.RoutedEventArgs.InvokeHandler(System.Delegate, System.Object)\n   at System.Windows.RoutedEventHandlerInfo.InvokeHandler(System.Object, System.Windows.RoutedEventArgs)\n   at System.Windows.EventRoute.InvokeHandlersImpl(System.Object, System.Windows.RoutedEventArgs, Boolean)\n   at System.Windows.UIElement.RaiseEventImpl(System.Windows.DependencyObject, System.Windows.RoutedEventArgs)\n   at System.Windows.UIElement.RaiseTrustedEvent(System.Windows.RoutedEventArgs)\n   at System.Windows.UIElement.RaiseEvent(System.Windows.RoutedEventArgs, Boolean)\n   at System.Windows.Input.InputManager.ProcessStagingArea()\n   at System.Windows.Input.InputManager.ProcessInput(System.Windows.Input.InputEventArgs)\n   at System.Windows.Input.InputProviderSite.ReportInput(System.Windows.Input.InputReport)\n   at System.Windows.Interop.HwndMouseInputProvider.ReportInput(IntPtr, System.Windows.Input.InputMode, Int32, System.Windows.Input.RawMouseActions, Int32, Int32, Int32)\n   at System.Windows.Interop.HwndMouseInputProvider.FilterMessage(IntPtr, MS.Internal.Interop.WindowMessage, IntPtr, IntPtr, Boolean ByRef)\n   at System.Windows.Interop.HwndSource.InputFilterMessage(IntPtr, Int32, IntPtr, IntPtr, Boolean ByRef)\n   at MS.Win32.HwndWrapper.WndProc(IntPtr, Int32, IntPtr, IntPtr, Boolean ByRef)\n   at MS.Win32.HwndSubclass.DispatcherCallbackOperation(System.Object)\n   at System.Windows.Threading.ExceptionWrapper.InternalRealCall(System.Delegate, System.Object, Int32)\n   at System.Windows.Threading.ExceptionWrapper.TryCatchWhen(System.Object, System.Delegate, System.Object, Int32, System.Delegate)\n   at System.Windows.Threading.Dispatcher.LegacyInvokeImpl(System.Windows.Threading.DispatcherPriority, System.TimeSpan, System.Delegate, System.Object, Int32)\n   at MS.Win32.HwndSubclass.SubclassWndProc(IntPtr, Int32, IntPtr, IntPtr)\n   at MS.Win32.UnsafeNativeMethods.DispatchMessage(System.Windows.Interop.MSG ByRef)\n   at System.Windows.Threading.Dispatcher.PushFrameImpl(System.Windows.Threading.DispatcherFrame)\n   at System.Windows.Threading.Dispatcher.PushFrame(System.Windows.Threading.DispatcherFrame)\n   at System.Windows.Application.RunDispatcher(System.Object)\n   at System.Windows.Application.RunInternal(System.Windows.Window)\n   at System.Windows.Application.Run(System.Windows.Window)\n   at WpfApp4.App.Main()\n</code></pre>\n<p>When this message can be quite helpful, especially in the case of Win32Exceptions.</p>\n<p>Hopefully this is a straight forward answer but I'm struggling to find it.</p>\n"
