          "link": "https://stackoverflow.com/questions/65307146/how-to-print-box-drawing-character-using-esc-pos",
          "title": "How to print box drawing character using ESC/POS",
          "body": "<p>I would like to print box drawing character using ESC/POS to Epson LQ310 dot matrix printer in android app. The sample printout would be as below,</p>\n<p><a href=\"https://i.stack.imgur.com/HNdo2.png\" rel=\"nofollow noreferrer\"><img src=\"https://i.stack.imgur.com/HNdo2.png\" alt=\"enter image description here\" /></a></p>\n<p>Initially, i am using underscore and vertical bar to draw its borders. But the printout turned out to be with broken line. I would like to having continuous line instead.</p>\n<p>Then i saw there is box drawing character with ASCII code as image attached,\n<a href=\"https://i.stack.imgur.com/UHgPY.png\" rel=\"nofollow noreferrer\"><img src=\"https://i.stack.imgur.com/UHgPY.png\" alt=\"Some characters for box drawing\" /></a></p>\n<p>Using those ASCII code info, i tried to print but the printout become other characters.\nI found out that it is due to different character set used in printer</p>\n<p>Checking through the ESC/P command, there is only these 2 commands regarding character set.\nESC R\nESC t</p>\n<p>Sample code i done as below, I just tried a few characters but it's to no avail.How to register the PC850 character set and use it using ESC t command?</p>\n<pre><code>    char[] verticalLine = {0x00B3}; \n    char[] verticalPlusLeft = {0x00B4};\n    char[] upperRightCorner = {0x00BF};\n    char[] bottomRightCorner = {0x00D9};\n    char[] upperLeftCorner = {0x00DA};\n    char[] bottomLeftCorner = {0x00C0};\n    char[] horizonLine = {0x00C4};\n    char[] verticalPlusRight = {0x00C3};\n    char[] charMultiLangual = {0x001B, 0x0074, 0x0002}; //ESC,t,2(PC850)\n    char[] charCodeDefault = {0x001B, 0x0074, 0x0000}; //ESC,t,0(Default)\n\n    strPrintFooterTitle = new String(charMultiLangual);\n    strPrintFooterTitle += printFooter(new String(upperLeftCorner), &quot;TYPE&quot;, new   String(verticalLine), &quot;QTY&quot;,new String(upperRightCorner)) + &quot;\\n&quot;;\n            strPrintFooterBig = printFooter(new String(verticalLine),&quot;Crate - Big&quot;, &quot;|&quot;, &quot;5&quot;,&quot;|&quot;) + &quot;\\n&quot;;\n            strPrintFooterSmall = printFooter(&quot;|&quot;,&quot;Crate - Small&quot;, &quot;|&quot;, &quot;10&quot;,&quot;|&quot;) + &quot;\\n&quot;;\n            strPrintFooter = new String(charCodeDefault);\n</code></pre>\n<p>Any help is much appreciated. Thanks</p>\n"
