          "title": "how to quickly calculate distance with pandas longitudes and latitudes data?",
          "body": "<p>I have a pandas table with different position data.For example, city of New York has a lot of schools ,hospitals, shops etc. Each has its own longitude and latitude.</p>\n<p>I have 40000 rows data and two column data(longitude and latitude). I want to calculate distance between them( 40000*40000 total).</p>\n<p>I used haversine formula (<a href=\"https://stackoverflow.com/questions/4913349/haversine-formula-in-python-bearing-and-distance-between-two-gps-points\">Haversine Formula in Python (Bearing and Distance between two GPS points)</a>) to do that with pandas.</p>\n<p>Simple code as:</p>\n<pre><code>results=df.apply(lambda x:haversine(x[&quot;lon&quot;],x[&quot;lat&quot;],test_lon,test_lat)\n</code></pre>\n<p>I use every row as test_lon,test_lat, and need 10 hours to calculate them. I can't believe why need so long to do that.</p>\n<p>Any one has good idea to do it quickly?</p>\n"
