          "body": "<p>I wanted to check the read permission of other group for all the files under /tmp/ap/<em>/jets/</em>/conf/.\nWhen I do debug of find_config I am able to see the values of roth. But please let me know how to print those values alone.</p>\n<pre class=\"lang-yaml prettyprint-override\"><code> - name: List config   \n    shell: ls -d /tmp/ap/*/jets/*/conf/\n    register: config\n  - name: Status\n    find:\n      paths: &quot;{{item}}&quot;\n    loop: &quot;{{config.stdout_lines}}&quot;\n    register: find_config\n  - debug:\n     msg: &quot;{{item.files.roth}}&quot;\n    loop: &quot;{{find_config.results}}&quot;\n</code></pre>\n<p>I also need to do a check like when roth is true then print FAIL.\nI need something like below:</p>\n<pre class=\"lang-yaml prettyprint-override\"><code>  - debug:\n     msg: &quot;FAIL&quot;\n    when: item.files.roth\n    loop: &quot;{{find_config.results}}&quot;\n</code></pre>\n"
