          "title": "Android - App called by another app - extras not evolving when app in background",
          "body": "<p>I have an android app &quot;A&quot; which calls another app &quot;B&quot; with parameters through :</p>\n<pre><code>        Intent intent = ApplicationContext.PackageManager.GetLaunchIntentForPackage(&quot;com.MyCompany.B&quot;);\n        \n\n        if(intent != null)\n        {\n\n            IList&lt;Java.Lang.Integer&gt; lstParam = new List&lt;Java.Lang.Integer&gt;()\n                {\n                    new Java.Lang.Integer(1111111),\n                    new Java.Lang.Integer(2222222)\n                };\n            intent.PutIntegerArrayListExtra(&quot;LstParam &quot;, lstParam );\n            StartActivity(intent);\n        }\n</code></pre>\n<p>So far so good, it works like a charm. B is launched (with splashscreen) and has correct extra &quot;LstParam&quot;.</p>\n<p>Issue is comming when B is in the background... A succeeds to launch B but LstParam extra is never received. It sounds like B actual intent is not refreshed...</p>\n<p>Thanks in advance to any helps...</p>\n"
