import React, { useState } from 'react';
import { Nav, Navbar, Form, FormControl, Button, Container} from 'react-bootstrap';
import logo from '../logo.svg';
import axios from 'axios';

export default function Header(){
    const [islogin, setIslogin] = useState(false);
    const [me, setMe] = useState([]);
        function login(){
            window.SE.authenticate({
                success: function(data){
                    alert(
                        'User ...' + data.networkUsers[0].account_id + 'got...' + data.accessToken
                    );
                    setIslogin(true);
                    console.log(data);
                    axios.get(`https://api.stackexchange.com/2.2/users/${data.networkUsers[0].user_id}?order=desc&sort=reputation&site=stackoverflow&key=eZoZLL*)llKhER8pMMusAw((`)
                    .then((resp) => {
                        setMe(resp.data.items)
                    })
                    console.log(me)
                },
                error: function(data){
                    alert('An error occurred:\n' + data.errorName + '\n' + data.errorMessage)
                },
                networkUsers: true
            });
        }
        return(
            <>
            <Navbar bg='dark' variant='dark' collapseOnSelect expand='md'>
                <Container>
                    <Navbar.Brand href='#/'>
                        <img 
                            alt='logo'
                            src={logo}
                            width='30'
                            height='30'
                            className='d-inline-block align-top'
                        />
                        USOF
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls='responsive-navbar-nav' />
                    <Navbar.Collapse id='responsive-navbar-nav'>
                        <Nav className='mr-auto'>
                            <Nav.Link href='#/'>Main</Nav.Link>
                            <Nav.Link href='#/users'>Users</Nav.Link>
                            <Nav.Link href='#/tags'>Tags</Nav.Link>
                        </Nav>
                        <Form inline>
                            <FormControl type='text' placeholder='Search' className='mr-sm-2' />
                            <Button variant='outline-success'>Search</Button>
                        </Form>                         
                            {islogin ? 
                                <div>
                                    {/* <img src={me[0].profile_image} />
                                    <p dangerouslySetInnerHTML={{__html:me.display_name}}></p> */}
                                    <p>login</p>
                                </div> : 
                                <Button variant='outline-info' className='ml-3' onClick={() => login()}>Login</Button>
                            }
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            
            </>
        )
    }


    // /2.2/access-tokens/{accessTokens}/invalidate